nav {
    background: rgba(0, 0, 0, 0.2);
    width: max-content;
    display: block;
    padding: 0.7rem, 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 4rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 5rem;
    backdrop-filter: blur(1.5px);
}

nav a {
    background: transparent;
    padding: 1.5rem;
    border-radius: 90%;
    display: flex;
    color: var(--color-white);
    font-size: 1.5rem;
}

nav a:hover {
    background: rgba(0, 0, 0, .6);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}